import MasterPage from '../components/Master';
import Precio from '../components/Precio';
import Noticias from '../components/Noticias';
import Eventos from '../components/Eventos';
import fetch from 'isomorphic-unfetch';

const Index = (props) => (
	<MasterPage>
		<div className="row">
			<div className="col-12">
				<h2>Precio del Bitcoin</h2>
				<Precio
					precio={props.precioBitcoin}
				/>
			</div>
			<div className="col-md-8">
				<h2>Noticias sobre Bitcoin</h2>
				<Noticias
					noticias={props.noticias}
				/>
			</div>
			<div className="col-md-4">
				<h2>Pr√≥ximos Eventos Bitcoin</h2>
				<Eventos
					eventos={props.eventos}
				/>
			</div>
		</div>
	</MasterPage>
);

Index.getInitialProps = async () => {
	let f = new Date();
	const date = `${f.getFullYear()}-${(f.getMonth() === 0)?'0'+(f.getMonth()+1):(f.getMonth()<10)?'0'+f.getMonth():f.getMonth()}-${f.getDate()}`;
	const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
	const noticias = await fetch(`http://newsapi.org/v2/everything?q=bitcoin&from=${date}&sortBy=publishedAt&apiKey=d85696289f8043719be66f4d4b455600&language=es`);
	// const eventos = await fetch('http://newsapi.org/v2/everything?q=bitcoin&from=2020-01-20&sortBy=publishedAt&apiKey=d85696289f8043719be66f4d4b455600&language=es')
	const resPrecio = await precio.json();
	const resNoticias = await noticias.json();
	// const resEventos = await eventos.json();

	return {
		precioBitcoin: resPrecio.data.quotes.USD,
		noticias : resNoticias.articles,
		eventos : resNoticias.articles
	}
}

export default Index;